* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(169, 169, 248);
}

.btn {
  background-color: white;
  color: rgb(150, 27, 150);
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  margin: 15px;
  padding: 5px;
  border: 2px solid rgb(150, 27, 150);
  border-radius: 5px;
  box-shadow: 0 12px rgb(117, 15, 117), 0 18px rgba(0, 0, 0, 0);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;
}

.btn:hover {
  background-color: rgb(194, 98, 194);
  color: rgb(169, 169, 248);
  top: 4px;
  box-shadow: 0 8px rgb(117, 15, 117), 0 14px rgba(0, 0, 0, 0);
}

.btn:active {
  top: 8px;
  box-shadow: 0 4px rgb(117, 15, 117), 0 10px rgba(0, 0, 0, 0);
}

.disabled .btn {
  cursor: default;
}
.disabled .btn:hover {
  top: 0;
  background: #ffffff;
  color: rgb(169, 169, 248);
  box-shadow: 0 12px rgb(117, 15, 117), 0 18px rgba(0, 0, 0, 0.4);
}

.btn.disabled {
  cursor: default;
}
.btn.disabled:hover {
  top: 0;
  background: #ffffff;
  color: rgb(150, 27, 150);
  box-shadow: 0 12px rgb(117, 15, 117), 0 18px rgba(0, 0, 0, 0.4);
}

.game {
  display: flex;
  flex-wrap: wrap;
}

.game-info {
  margin: 15px;
  color: white;
}

.board > div {
  display: flex;
}
